import {Component, HostListener, OnInit, ViewChild} from '@angular/core';
import {ContentPanelService} from "./content-panel.service";

@Component({
  selector: 'app-content-panel',
  templateUrl: './content-panel.component.html',
  styleUrls: ['./content-panel.component.css']
})
export class ContentPanelComponent implements OnInit {

  @ViewChild('contentPage', {static: true}) contentPage;
  width = '220px';
  minWidth = '220px';
  marginLeft = '220px';
  widthContent = 'calc(100% - 220px)';

  constructor(private contentService: ContentPanelService) {
    this.contentService.pageScroll.subscribe(() => {
      this.contentPage.nativeElement.scrollTo(0, 0)
    })
  }

  ngOnInit() {
    if (window.outerWidth < 1000) {
      this.setMobile()
    }
  }

  // toggleNavBar(toggle) {
  //   this.toggle = toggle;
  //   if (toggle.toggle) {
  //     this.setWeb()
  //     toggle.toggle = false;
  //   } else {
  //     this.setMobile()
  //     toggle.toggle = true;
  //   }
  // }

  toggleNavBar(toggle) {
    if (window.outerWidth < 1000) {
      if (toggle) {
        this.setWeb()
      } else {
        this.setMobile()
      }
    }
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (window.outerWidth < 1000) {
      this.setMobile()
    } else {
      this.setWeb()
    }
  }

  setMobile() {
    this.width = '0px';
    this.minWidth = '0px';
    this.marginLeft = '0px';
    this.widthContent = 'calc(100%)';
  }

  setWeb() {
    this.width = '220px';
    this.minWidth = '220px';
    this.marginLeft = '220px';
    this.widthContent = 'calc(100% - 220px)';
  }
}
