import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {DashboardComponent} from "./content-panel/dashboard/dashboard.component";
import {ContentPanelComponent} from "./content-panel/content-panel.component";
import {InventoryComponent} from "./content-panel/inventory/inventory.component";
import {OrderAddComponent} from "./content-panel/purchase-order/order-add/order-add.component";
import {OrderViewComponent} from "./content-panel/purchase-order/orders-list/order-view/order-view.component";
import {OrdersListComponent} from "./content-panel/purchase-order/orders-list/orders-list.component";
import {GrnsListComponent} from "./content-panel/grn/grns-list/grns-list.component";
import {GrnViewComponent} from "./content-panel/grn/grns-list/grn-view/grn-view.component";
import {GrnAddComponent} from "./content-panel/grn/grn-add/grn-add.component";
import {SupplierComponent} from "./content-panel/supplier/supplier.component";


const routes: Routes = [
  {
    path: '',
    redirectTo: 'content/inventory',
    pathMatch: 'full'
  },
  {
    path: 'content',
    component: ContentPanelComponent,
    children: [
      {
        path: 'inventory',
        component: InventoryComponent
      },
      {
        path: 'supplier',
        component: SupplierComponent
      },
      {
        path: 'purchase_order',
        children: [
          // {
          //   path: '',
          //   redirectTo: 'list',
          //   pathMatch: 'full'
          // },
          {
            path: 'list',
            component: OrdersListComponent
          },
          {
            path: 'view',
            component: OrderViewComponent
          },
          {
            path: 'add',
            component: OrderAddComponent
          }
        ]
      },
      {
        path: 'grn',
        children: [
          // {
          //   path: '',
          //   redirectTo: 'list',
          //   pathMatch: 'full'
          // },
          {
            path: 'list',
            component: GrnsListComponent
          },
          {
            path: 'view',
            component: GrnViewComponent
          },
          {
            path: 'add',
            component: GrnAddComponent
          }
        ]
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  // imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
