import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {GrnAddService} from "./grn-add.service";
import {AlertBoxComponent} from "../../../alert-box/alert-box.component";
import {AlertBoxService} from "../../../alert-box/alert-box.service";
import {Subject} from "rxjs";

@Component({
  selector: 'app-grn-add',
  templateUrl: './grn-add.component.html',
  styleUrls: ['./grn-add.component.css']
})
export class GrnAddComponent implements OnInit {

  alertBox = {
    alert: false,
    msg: '',
    value: ''
  };
  fromPO = false;

  items = [
    {
      code: 214332,
      desc: 'PHENYCOF COUGH SYRUP 100ML',
      ps: 1,
      ws: 204.41,
      qty: 12,
      free: 0.00.toFixed(2),
      perce: 4.00.toFixed(2),
      discount: 98.12,
      amount: 2254.80,
      newRow: false
    },
    {
      code: 214331,
      desc: 'PHENYCOF JUNIOR COUGH SYRUP 6',
      ps: 1,
      ws: 164.35,
      qty: 24,
      free: 0.00.toFixed(2),
      perce: 4.00.toFixed(2),
      discount: 158.78,
      amount: 3766.62,
      newRow: false
    },
    {
      code: '',
      desc: '',
      ps: 0,
      ws: 0.00.toFixed(2),
      qty: 0,
      free: 0.00.toFixed(2),
      perce: 0.00.toFixed(2),
      discount: 0.00.toFixed(2),
      amount: 0.00.toFixed(2),
      newRow: true
    }
  ];

  constructor(private grnAddService: GrnAddService, private alertService: AlertBoxService) {
  }

  ngOnInit() {
    window.scrollTo(0, 0)
    this.fromPO = false;
    this.setGrn()
  }

  addRow() {
    this.items[this.items.length - 1].newRow = false;
    this.items.push({
      code: '',
      desc: '',
      ps: 0,
      ws: 0.00.toFixed(2),
      qty: 0,
      free: 0.00.toFixed(2),
      perce: 0.00.toFixed(2),
      discount: 0.00.toFixed(2),
      amount: 0.00.toFixed(2),
      newRow: true
    })
  }

  removeRow(index, item) {
    this.alertBox.alert = true;
    this.alertBox.msg = 'Do you want to remove';
    this.alertBox.value = item.desc + ' (' + item.code + ') ?'
    this.alertService.reply.observers = [];
    this.alertService.reply.subscribe(reply => {
      if (reply) {
        this.items.splice(index, 1);
      }
      this.alertBox.alert = false;
    })
  }

  setGrn() {
    if (this.grnAddService.orders !== undefined) {
      this.items = this.grnAddService.orders;
      for (let item of this.items) {
        item.ps = 0;
        item.ws = 0.00.toFixed(2);
        item.free = 0.00.toFixed(2);
        item.perce = 0.00.toFixed(2);
        item.discount = 0.00.toFixed(2);
        item.amount = 0.00.toFixed(2);
      }
      this.fromPO = true;
      this.grnAddService.orders = undefined;
    }
  }
}
